/* eslint-disable */
import request from "./http";
import * as util from "../util/common";
import { golbalConfig } from "./apiConfig";
// import intl from "react-intl-universal";


/**
 * @param 
 * @returns 应急指挥 接警记录
 */
export const getAlarmList = async () => {
  const param = {
    classifyCodes: "",
    customCondition: {},
    permissionCode: "SESECD_1.0.0_alarmRecord_alarmHandleList",
    pageNo: 1,
    paging: true,
    pageSize: 999,
  }
  const res = await request.httpPost(`${golbalConfig.alarmListUrl}`, 'post', param);
  if (res && res.data && res.data.code === 200 && res.data.data.result) {
    return res.data.data.result
  } else {
    return [];
  }
};
/**
 * @param 
 * @returns 应急指挥 应急事件
 */
export const getEventList = async (type = 1) => { // 1 结束 0 未结束
  const param = {
    classifyCodes: "",
    fastQueryCond: `{"viewCode":"SESECD_1.0.0_alarmRecord_alarmEventList","modelAlias":"almAlarmRecord","condName":"fastCond","remark":"fastCond","subconds":[{"type":"0","columnName":"IS_OVER","dbColumnType":"BOOLEAN","operator":"=","paramStr":"?","value":"${type}"}]}`,
    customCondition: {},
    permissionCode: "SESECD_1.0.0_alarmRecord_alarmEventList",
    pageNo: 1,
    paging: true,
    pageSize: 999,
  }
  const res = await request.httpPost(`${golbalConfig.eventListUrl}`, 'post', param);
  if (res && res.data && res.data.code === 200 && res.data.data.result) {
    return res.data.data.result
  } else {
    return [];
  }
};
/**
 * @param 报警id 报警等级
 * @returns 应急指挥 接警处警
 */
export const changeAlarmState = async (param) => {
  const params = {
      alarmIds: [param.alarmId],
      mesCode: [],
      drillPlanId: "",
      processState: "SESECD_processState/001",
      alarmLevel: param.alarmLevel || 'SESECD_alarmLevel/001',
      eventType: "SESECD_eventType/001",
      process: "",
      incident: true,
      voiceConfigId: "",
  }
  const res = await request.httpPost(`${golbalConfig.changeAlarmUrl}`, 'post', params);
  if(res && res.data && res.data.code === 200) {
    return true
  } else {
    return false
  }
};

