/**
 * @param {事件id} id 
 * @returns 应急事件的基本信息
 */
export const getEventData = async (id) => {
  const res = await request.http(`${golbalConfig.eventDataUrl}${id}`);
  if (res && res.code === 200) {
    return res.data
  } else {
    return {};
  }
};
