export const dateFormat = (date: Date, fmt: string =  "YYYY-mm-dd HH:MM:SS", ) => {    
    let ret;
    const opt: Record<string, string> = {
        'Y+': date.getFullYear().toString(), // 年
        'm+': (date.getMonth() + 1).toString(), // 月
        'd+': date.getDate().toString(), // 日
        'H+': date.getHours().toString(), // 时
        'M+': date.getMinutes().toString(), // 分
        'S+': date.getSeconds().toString(), // 秒
    };
    const opts = new Map<string, string>();
    for (let k in opt) {
        ret = new RegExp('(' + k + ')').exec(fmt);
        if (ret) {
            fmt = fmt.replace(
                ret[1],
                ret[1].length == 1 ? opt[k] : opt[k].padStart(ret[1].length, '0'),
            );
        }
    }
    return fmt;
};

export const GetRequest = (urlString: string) => {
    const url = decodeURI(urlString);
    let theRequest: Record<string, any> = new Object();
    if (url.indexOf('?') != -1) {
        const str = url.split('?');
        let strs = str[1].split('&');
        for (var i = 0; i < strs.length; i++) {
            strs[i] = strs[i].split('#')[0];
            theRequest[strs[i].split('=')[0]] = unescape(strs[i].split('=')[1]);
        }
    }

    return theRequest;
};

export const GetUploadType = function (
    workflowEnabled: boolean,
    modelCode: string,
) {
    const code = modelCode.split('_');
    code.splice(1, 1);
    return workflowEnabled ? 'Table' : code.join('_');
};

export const DataURItoBlob = (base64Data: string) => {
    let byteString;
    if (base64Data.split(',')[0].indexOf('base64') >= 0) {
        byteString = atob(base64Data.split(',')[1]);
    } else {
        byteString = unescape(base64Data.split(',')[1]);
    }
    let mimeString = base64Data
        .split(',')[0]
        .split(':')[1]
        .split(';')[0];
    let ia = new Uint8Array(byteString.length);
    for (let i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
    }
    return new Blob([ia], { type: mimeString });
};

export const dataURLtoBlob = (dataurl: string, filename: string) => {
    let arr: any = dataurl.split(','),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
    while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
    }
    return new File([u8arr], filename, { type: mime });
}
