import axios from 'axios';
import * as util from '../util/common';
// import intl from 'react-intl-universal';
const getCookie = (cname) => {
  const name = `${cname}=`;
  const ca = document.cookie.split(';');
  for (let i = 0; i < ca.length; i += 1) {
    const c = ca[i].trim();
    if (c.indexOf(name) === 0) {
      return c.substring(name.length, c.length);
    }
  }
  return '';
};
const ticket = getCookie('suposTicket')
axios.defaults.baseURL =  window.location.hostname === 'localhost' ? 'http://10.54.4.40:8080':'';
axios.defaults.headers.common['Access-Control-Allow-Methods'] = 'PUT,POST,GET,DELETE,OPTIONS';
axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*';
axios.defaults.headers.common['Access-Control-Allow-Headers'] = 'X-Requested-With,Content-Type';
axios.defaults.headers.common['Accept'] = 'application/json; charset=utf-8';
axios.defaults.headers.common['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
axios.defaults.headers.common['Cache-Control'] = 'no-cache';
axios.defaults.headers.common['Authorization'] = `Bearer ${ticket}`;
axios.defaults.timeout = '60000';
axios.defaults.withCredentials = true;

axios.interceptors.response.use(
  function(response) {
    // 对响应数据做点什么
    return response;
  },
  function(error) {
    if (error.message.includes('timeout')) {
      //util.showErrorMsg(intl.get('util.res.timeout'), error.message);
      return Promise.reject(error);
    } else if (error.message.includes('500')) {
      //util.showErrorMsg(intl.get('util.res.error'), error.message);
      return Promise.reject(error);
    } else {
      //util.showErrorMsg(intl.get('util.res.unknowError'), error.message);
      return Promise.reject(error);
    }
  },
);

const getTicket = (name) => {
  let ticket = window.localStorage.getItem(name);
  if (ticket === null) {
    return "";
  }
  return ticket;
};

//数据请求类
class Request {
  async http(url, method = 'GET', params = {}) {
    return await axios({
      url: url,
      method: method,
      headers: {
        Authorization: `Bearer ${ticket}`,
      },
    })
      .then(result => {
        if (result.status === 200) {
          return result.data;
        } else {
          console.log('请求错误码 ：' + result.status);
        }
      })
      .catch(error => console.log(error));
  }

  async httpPost(url, method = 'post', params = {}) {
    return await axios({
      url: url,
      method: method,
      headers: {
        'Content-Type': 'application/json; charset=utf-8',
        Accept: 'application/json; charset=utf-8',
        Authorization: `Bearer ${ticket}`,
      },
      data: params,
    })
      .then(result => {
        if (result.status === 200) {
          return result;
        } else {
          console.log('请求错误码 ：' + result.status);
        }
      })
      .catch(error => console.log(error));
  }
}

export default new Request();
